
 .id-card {
    background-color: white;
    width: 350px;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.id-card .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid #000;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

.id-card .header .logo {
    width: 50px;
    height: auto;
}

.id-card .header h1 {
    font-size: 14px;
    text-align: center;
    flex-grow: 1;
    margin: 0 10px;
}

.id-card .profile-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.id-card .profile-section .profile-pic {
    width: 80px;
    height: 100px;
    border: 1px solid #000;
}

.id-card .profile-section .details {
    flex-grow: 1;
    margin-left: 20px;
}

.id-card .profile-section .details p {
    margin: 5px 0;
}

.id-card .profile-section .qr-code {
    width: 80px;
    height: 80px;
}
