.student-list-container {
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  

  .student-search-box{
    padding: 5px;
    font-size: 20px;
    margin: 10px;
    border-radius: 5px;
  }
  
  /* Student cards styles */
  .student-cards {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    width: 50%;
    justify-content: center;
    align-items: center;

  }
  
  .student-card {
    flex: 1 1 calc(50% - 20px);
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    background-color: #fafafa;
    cursor: pointer;
    transition: transform 0.2s, box-shadow 0.2s;
    font-weight: 600;
    color: black;
  }
  
  .student-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .student-card h4 {
    margin: 0 0 10px;
    font-size: 18px;
    color: #333;
  }
  
  .student-card p {
    margin: 0;
    font-size: 1rem;
    color: #000000;
  }
  
  /* Pagination styles */
  .pagination {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .pagination button {
    background-color: #1B756B;
    color: #fff;
    border: none;
    padding: 10px 15px;
    margin: 0 5px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .pagination button:hover {
    background-color: #0056b3;
  }