body {
    font-family: "Poppins";
  }
  
  .landing-page {
    background-color: #ffffff;
    color: #1b756b;
  }
  
  /* Header Styling */
  .header {
    background-color: #1b756b;
    color: white;
    padding: 10px;
  }
  
  #MOE {
    width: 200px;
    margin-left: 10px;
    object-fit: cover;
  }
  
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navbar ul {
    list-style-type: none;
    display: flex;
    gap: 20px;
  }
  
  .navbar a {
    color: white;
    text-decoration: none;
    font-weight: bold;
  }
  
  .navbar a:hover {
    text-decoration: underline;
  }
  
  /* Hero Section */
  .hero {
    text-align: left;
    padding: 50px;
    background-color: #f0f7f7;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
  }
  
  .hero h1 {
    font-size: 2.5rem;
    color: black;
  }
  .hero p {
    color: black;
    font-size: x-large;
  }
  
  .hero img {
    width: 100%;
    max-width: 500px;
    transform: scale(1.05);
    mix-blend-mode: multiply;
  }
  
  .about {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  
  .about-content {
    max-width: 700px;
  }
  .about-content h1 {
    color: black;
    font-size: xx-large;
  }
  
  .about-content p {
    color: black;
    font-size: larger;
  }
  /* About APAAR Section */
  .about,
  .get-apaar,
  .benefits {
    padding: 40px;
    text-align: left;
    list-style: square !important;

  }
  
  .about img,
  .get-apaar img {
    width: 100%;
    max-width: 500px;
    margin-top: 20px;
  }
  
  .benefits {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  
  .benefits video {
    max-width: 60%;
  }
  
  .benefits-content{
      color: black;
  }
  .benefits-content h1{
      font-size: xx-large;
  }
  .benefits-content li{
      font-size: larger;
  }
  /* CTA Section */
  .cta {
    background-color: #1b756b;
    color: white;
    padding: 50px;
    text-align: center;
  }
  
  .cta-button {
    padding: 15px 30px;
    font-size: 1.2em;
    background-color: white;
    color: #1b756b;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .cta-button:hover {
    background-color: #f0f7f7;
  }
  
  /* Footer Styling */
  .footer {
    background-color: #1b756b;
    color: white;
    padding: 20px;
    text-align: center;
  }
  
  .footer-links {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 10px;
  }
  
  .footer a {
    color: white;
    text-decoration: none;
  }
  
  .footer a:hover {
    text-decoration: underline;
  }
  .apaaar-steps-container {
    max-width: 90vw;
    display: flex;
    justify-content: space-between; /* Ensure even spacing between steps */
    flex-wrap: wrap;
    gap: 20px;
    margin: auto;
    margin-bottom: 27px;
    position: relative;
  }
  
  .apaaar-step-card {
    background: linear-gradient(145deg, #a8e063, #56ab2f);
    border-radius: 50px;
    padding: 20px;
    width: 250px;
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    position: relative;
    cursor: pointer;
  }
  
  .apaaar-step-card:not(:last-child)::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translateY(-50%);
    width: 500px; /* Adjust this width for spacing between cards */
    height: 2px; /* Height of the dashed line */
    background-color: transparent;
    border-top: 2px dashed #000000; /* Horizontal dashed line */
  }
  
  .apaaar-step-card:hover {
    transform: scale(1.05);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
  }
  
  .step-number {
    background-color: #4caf50;
    color: white;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin: 0 auto 10px;
    font-size: 20px;
    font-weight: bold;
  }
  
  .step-title {
    font-size: 18px;
    margin-bottom: 10px;
    color: #ffffff;
  }
  
  .step-description {
    color: #fff;
    font-size: 14px;
  }
  
  .apaaar-step-card:hover .step-description {
    display: block;
    margin-top: 10px;
  }
  
.landing-page h1{
    font-size: 3rem !important;
    font-weight: bolder !important;
    color: black !important;
  }
  .actr span{
    color: #1b756b;
  }